@charset "UTF-8";

:root {
  --content-width: 1566px;
  --gutter: 16px;
  --mob-padding: 32px;
}

.container {
  width: 100%;
  max-width: calc(var(--content-width) + var(--mob-padding) * 2);
  padding: 0 var(--mob-padding);
  margin: 0 auto;
}

.container-fluid {
  width: 100%;
  max-width: 100%;
}

.row {
  display: flex;
  flex-wrap: wrap;
  clear: both;
  margin-left: calc(var(--gutter) * -1);
  margin-right: calc(var(--gutter) * -1);
}

.row_no-gutter {
  margin-left: 0;
  margin-right: 0;
}

@font-face {
  font-family: 'PF';
  src: url("../fonts/pfdintextcomppro-boldital.ttf");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'PF';
  src: url("../fonts/pfdintextcomppro-italic.ttf");
  font-weight: 500;
  font-style: normal;
}

html {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, Helvetica, Arial, sans-serif;
}

main {
  height: 100%;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
  padding: 0;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

iframe,
button {
  border: 0;
}

table {
  border-spacing: 0;
}

button {
  cursor: pointer;
}

figure {
  margin: 0;
}

:root {
  --def-fs: 16px;
}

body {
  font-size: var(--def-fs);
  -webkit-text-size-adjust: 100%;
  text-rendering: optimizeSpeed;
  line-height: 1.4;
  font-weight: 400;
  color: #28282C;
  background: #fff;
}

ul,
ol {
  padding: 0;
  margin-bottom: 0;
  margin-top: 0;
}

ul {
  list-style-type: none;
}

a {
  color: red;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5 {
  color: inherit;
  font-weight: bold;
  margin-bottom: 0;
  margin-top: 0;
}

h1 {
  font-size: 32px;
}

h2 {
  font-size: 30px;
}

h3 {
  font-size: 24px;
}

h4 {
  font-size: 20px;
}

p {
  color: inherit;
  margin-top: 0;
  margin-bottom: 0;
}

.header__wrapper {
  box-sizing: border-box;
  min-width: 0;
  margin-left: var(--gutter);
  margin-right: var(--gutter);
  width: calc(100% * 1 - var(--gutter) * 2);
  flex-basis: calc(100% * 1 - var(--gutter) * 2);
  flex-grow: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: auto;
  left: 0;
  right: 0;
  top: 60px;
}

.main {
  position: relative;
}

.footer {
  background: #21262b;
  padding-top: 31px;
  padding-bottom: 31px;
  position: relative;
}

.footer::before {
  position: absolute;
  display: block;
  content: "";
  bottom: 100%;
  left: 0;
  right: 0;
  z-index: -1;
}

.footer__wrapper {
  justify-content: center;
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.footer__mob-icons {
  margin-right: 8px;
  display: flex;
  align-items: center;
}

.footer__mob-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 83.5%;
  display: flex;
  letter-spacing: 0.015em;
  color: #ffffff;
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-sizing: border-box;
  border-radius: 4px;
  height: 30px;
  padding: 0 12px;
  transition: 0.3s;
}

.footer__mob-icon:first-of-type {
  margin-right: 10px;
}

.footer__mob-icon:hover {
  transition: 0.3s;
  background: #fff;
  color: #000;
}

.footer__mob-icon:hover svg * {
  transition: 0.3s;
  fill: #000;
}

.footer__mob-icon svg {
  margin-right: 9px;
}

.footer__mob-icon svg * {
  transition: 0.3s;
  fill: #fff;
}

.footer__mob-icon span {
  width: max-content;
}

.footer__texts {
  margin-left: 8px;
  margin-right: 8px;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  color: #a0a6ab;
}

.footer__soc-icons {
  margin-left: 8px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.footer__soc-icon {
  height: 30px;
  width: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-sizing: border-box;
  border-radius: 4px;
  margin-left: 10px;
}

.footer__soc-icon:hover {
  transition: 0.3s;
  background: #fff;
  color: #000;
}

.footer__soc-icon:hover svg * {
  transition: 0.3s;
  fill: #000;
}

.footer__soc-icon:first-of-type {
  margin-left: 0;
}

.footer__soc-icon svg * {
  transition: 0.3s;
  fill: #fff;
}

.promo {
  position: relative;
  height: 100%;
  overflow: hidden;
}

.promo::after {
  position: absolute;
  content: "";
  display: block;
  top: -100%;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  background: url("../../img/locked.png");
  transition: .6s;
  opacity: 0;
}

.promo > img {
  z-index: -1;
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.promo__wrapper {
  display: flex;
  flex-wrap: wrap;
  clear: both;
  margin-left: calc(var(--gutter) * -1);
  margin-right: calc(var(--gutter) * -1);
  padding-top: 163px;
  height: 100%;
  flex-wrap: nowrap;
}

.promo__left-img {
  box-sizing: border-box;
  min-width: 0;
  margin-left: var(--gutter);
  margin-right: var(--gutter);
  width: calc(100% * 0.5 - var(--gutter) * 2);
  flex-basis: calc(100% * 0.5 - var(--gutter) * 2);
  flex-grow: 0;
  display: flex;
  justify-content: end;
  align-items: flex-end;
}

.promo__info-block {
  box-sizing: border-box;
  min-width: 0;
  margin-left: var(--gutter);
  margin-right: var(--gutter);
  width: calc(100% * 0.5 - var(--gutter) * 2);
  flex-basis: calc(100% * 0.5 - var(--gutter) * 2);
  flex-grow: 0;
  transform: skewX(349deg);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.promo__info-block_title {
  font-family: "PF";
  font-weight: bold;
  font-size: 94.8605px;
  line-height: 100%;
  color: #12F24B;
  transform: skewX(11deg);
}

.promo__info-block_count {
  font-family: "PF";
  font-weight: bold;
  color: #fff;
  display: flex;
  align-items: center;
  margin-bottom: 37px;
}

.promo__info-block_count-num {
  font-size: 163.785px;
  line-height: 99%;
  position: relative;
  z-index: 1;
  padding: 30px;
  transform: skewX(11deg);
}

.promo__info-block_count-num::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: -10px;
  bottom: 0;
  z-index: -1;
  background: #12F24B;
  margin: auto;
  width: 52px;
  height: 112px;
  border-radius: 15px;
  transform: skewX(349deg);
}

.promo__info-block_count-num::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: -1;
  border-radius: 15px;
  box-shadow: inset 0px 4.3097px 4.3097px rgba(255, 255, 255, 0.27), inset 0px 19.3937px 23.7034px rgba(82, 242, 123, 0.06), inset 0px 3.23228px 14.0065px rgba(59, 208, 97, 0.29);
  backdrop-filter: blur(45.2519px);
  transform: skewX(349deg);
}

.promo__info-block_count-type {
  font-size: 84.8088px;
  line-height: 99%;
  position: relative;
  padding: 15px;
  z-index: 4;
  right: 20px;
  transform: skewX(11deg);
}

.promo__info-block_count-type::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: -6px;
  bottom: 0;
  z-index: -1;
  background: #12F24B;
  margin: auto;
  width: 27px;
  height: 58px;
  border-radius: 7px;
  transform: skewX(349deg);
}

.promo__info-block_count-type::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: -1;
  border-radius: 7px;
  box-shadow: inset 0px 4.3097px 4.3097px rgba(255, 255, 255, 0.27), inset 0px 19.3937px 23.7034px rgba(82, 242, 123, 0.06), inset 0px 3.23228px 14.0065px rgba(59, 208, 97, 0.29);
  backdrop-filter: blur(45.2519px);
  transform: skewX(349deg);
}

.promo__info-block_subtitle {
  font-family: "PF";
  font-weight: bold;
  font-size: 69px;
  line-height: 100%;
  color: #ffffff;
  text-transform: uppercase;
  margin-bottom: 43px;
  position: relative;
  right: 11px;
}

.promo__info-block_subtitle p {
  transform: skewX(11deg);
  right: -11px;
  position: relative;
}

.promo__info-block_btns {
  display: flex;
  align-self: flex-start;
  align-items: center;
  margin-bottom: 150px;
}

.promo__info-block_btns button {
  margin-right: 26px;
  position: relative;
  background: none;
  border: none;
  padding: 15px 23px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-family: "PF";
  font-style: italic;
  font-weight: bold;
  font-size: 37px;
  line-height: 99%;
  text-transform: uppercase;
}

.promo__info-block_btns button::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: -1;
  border-radius: 7px;
  background: #e91524;
  border-radius: 6px;
  transform: skewX(0);
  opacity: 1;
  filter: drop-shadow(0px 5.52574px 69.0717px rgba(233, 21, 36, 0.63));
  transition: .3s;
}

.promo__info-block_btns button span {
  transform: skewX(11deg);
}

.promo__info-block_btns button:hover {
  filter: drop-shadow(0px 5.52574px 69.0717px #e91524);
  transition: .3s;
}

.promo__info-block_btns button:hover img {
  left: 10px;
  transition: 0.3s;
}

.promo__info-block_btns button img {
  left: 0;
  transition: 0.3s;
  margin-left: 25px;
  position: relative;
}

.promo__info-block_btns a {
  font-family: "PF";
  font-weight: 500;
  font-size: 24px;
  line-height: 99%;
  text-decoration-line: underline;
  color: #ffffff;
  transition: 0.3s;
  opacity: 0.5;
}

.promo__info-block_btns a:hover {
  transition: 0.3s;
  opacity: 1;
}

.container {
  height: 100%;
}

.promo-locked {
  transition: .6s;
}

.promo-locked::after {
  transition: .6s;
  opacity: 1;
  top: 0;
}

.popup-main {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 808px;
  height: 780px;
  background: #f3f3f3;
  border-radius: 12px;
  overflow: hidden;
  transition: 0.6s;
  top: -500%;
}

.popup {
  height: 780px;
  overflow: auto;
  padding: 60px 30px 54px;
}

.popup::-webkit-scrollbar {
  width: 5px;
  /* ширина для вертикального скролла */
  background-color: #d1d1d1;
  position: relative;
  right: 5px;
}

.popup::-webkit-scrollbar-thumb {
  background-color: #0a9f30;
  box-shadow: inset 1px 1px 10px #0a9f30;
}

.popup::-webkit-scrollbar-thumb:hover {
  background-color: #0a9f30;
}

.popup__wrapper {
  display: flex;
  flex-direction: column;
}

.popup__close-btn {
  position: absolute;
  width: 36px;
  height: 36px;
  background: #ffffff;
  border-radius: 0px 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  right: 0;
  top: 0;
  transition: 0.3s;
  cursor: pointer;
}

.popup__close-btn svg {
  transition: 0.3s;
  transform: rotate(0deg);
}

.popup__close-btn svg * {
  transition: 0.3s;
  fill: #000;
}

.popup__close-btn:hover {
  transition: 0.3s;
  background: #0a9f30;
}

.popup__close-btn:hover svg {
  transition: 0.3s;
  transform: rotate(180deg);
}

.popup__close-btn:hover svg * {
  transition: 0.3s;
  fill: #fff;
}

.popup__instruction {
  margin-bottom: 45px;
}

.popup__instruction ul li {
  padding: 19px 30px;
  background: #fff;
  display: flex;
  justify-content: center;
  font-family: "PF";
  font-weight: bold;
  font-size: 25px;
  line-height: 125%;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 15px;
  position: relative;
}

.popup__instruction ul li::after {
  display: block;
  position: absolute;
  content: "";
  left: 0;
  right: 0;
  bottom: -10px;
  margin: auto;
  width: 0;
  height: 0;
  border-left: 22px solid transparent;
  border-right: 22px solid transparent;
  border-top: 10px solid #fff;
}

.popup__instruction_text {
  color: #21262b;
}

.popup__instruction_point {
  color: #0a9f30;
}

.popup__info {
  margin-bottom: 20px;
}

.popup__info_subtitle {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 125%;
  text-align: center;
  color: #21262b;
  padding-bottom: 30px;
}

.popup__info_title {
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 125%;
  text-align: center;
  color: #21262b;
}

.popup__steps {
  margin-bottom: 35px;
}

.popup__steps ul li {
  padding: 19px 30px;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  margin-bottom: 15px;
  position: relative;
}

.popup__steps ul li::after {
  display: block;
  position: absolute;
  content: "";
  left: 0;
  right: 0;
  bottom: -10px;
  margin: auto;
  width: 0;
  height: 0;
  border-left: 22px solid transparent;
  border-right: 22px solid transparent;
  border-top: 10px solid #fff;
}

.popup__steps_title {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 125%;
  text-align: center;
  color: #21262b;
  padding-bottom: 14px;
}

.popup__steps_subtitle {
  font-style: normal;
  font-size: 14px;
  line-height: 125%;
  text-align: center;
  color: #21262b;
}

.popup__btn {
  display: flex;
  justify-content: center;
  margin-bottom: 54px;
}

.popup__btn:last-child {
  margin-bottom: 0;
}

.popup__btn button {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "PF";
  font-weight: bold;
  font-size: 18px;
  line-height: 99%;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  position: relative;
  background: none;
  z-index: 2;
  padding: 10px;
}

.popup__btn button::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: -1;
  border-radius: 7px;
  background: linear-gradient(180deg, #fc3241 0%, #c80f1c 100%);
  border-radius: 6px;
  transform: skewX(0);
  opacity: 1;
  filter: drop-shadow(0px 5.52574px 69.0717px rgba(233, 21, 36, 0.63));
  transition: 0.3s;
  transform: skewX(349deg);
}

.popup__btn button:hover {
  filter: drop-shadow(0px 5.52574px 19.0717px #e91524);
  transition: 0.3s;
}

.popup__btn button:hover img {
  left: 10px;
  transition: 0.3s;
}

.popup__btn button img {
  left: 0;
  transition: 0.3s;
  margin-left: 25px;
  margin-right: 10px;
  position: relative;
}

.popup__info-text {
  margin-bottom: 60px;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 125%;
  color: #21262b;
}

.popup__info-text ul li {
  margin-bottom: 15px;
}

.popup-active {
  transition: 0.6s;
  top: 0;
}

@media screen and (max-width: 1120px) {
  .promo__left-img img {
    position: absolute;
    width: 574px;
    left: -22px;
  }

  .promo__info-block {
    position: relative;
    right: 10%;
  }
}

@media screen and (max-width: 1024px) {
  :root {
    --mob-padding: 16px;
  }

  .footer__wrapper {
    flex-wrap: wrap;
    justify-content: center;
  }

  .footer__texts {
    width: 100%;
    order: 3;
    margin-top: 8px;
  }

  .promo__info-block_title {
    font-size: 66px;
  }

  .promo__info-block_count {
    margin-bottom: 26px;
  }

  .promo__info-block_count-num {
    font-size: 114px;
    padding: 21px;
  }

  .promo__info-block_count-type {
    right: 10px;
    font-size: 59px;
    padding: 11px;
  }

  .promo__info-block_subtitle {
    font-size: 49px;
  }

  .promo__info-block_btns button {
    font-size: 24px;
  }

  .promo__info-block_btns a {
    font-size: 16px;
  }

  .popup-main {
    height: 708px;
  }

  .popup {
    height: 708px;
  }
}

@media screen and (max-width: 768px) {
  .footer::before {
    background: linear-gradient(180deg, rgba(86, 94, 102, 0) 0%, #21262b 100%);
  }

  .promo__left-img img {
    position: absolute;
    width: 682px;
    left: -192px;
  }

  .promo__info-block {
    width: calc(100% * 0.83333 - var(--gutter) * 2);
    flex-basis: calc(100% * 0.83333 - var(--gutter) * 2);
    flex-grow: 0;
  }

  .promo__info-block_title {
    font-size: 71px;
  }

  .promo__info-block_count-num {
    font-size: 124px;
  }

  .promo__info-block_count-type {
    font-size: 64px;
  }

  .promo__info-block_subtitle {
    font-size: 52px;
  }

  .popup-main {
    width: 708px;
    height: 904px;
  }

  .popup {
    height: 904px;
  }
}

@media screen and (max-width: 640px) {
  .footer::before {
    height: 148px;
    background: linear-gradient(180deg, rgba(86, 94, 102, 0) 0%, #21262b 100%);
  }

  .footer__mob-icons {
    margin-right: 0;
  }

  .footer__mob-icon {
    padding: 0 4px;
  }

  .footer__mob-icon:first-of-type {
    margin-right: 5px;
  }

  .footer__mob-icon svg {
    margin-right: 6px;
  }

  .footer__texts {
    font-size: 11px;
  }

  .promo__left-img {
    display: none;
  }

  .promo__info-block {
    right: 0;
    width: calc(100% * 1 - var(--gutter) * 2);
    flex-basis: calc(100% * 1 - var(--gutter) * 2);
    flex-grow: 0;
    align-items: center;
    transform: skewX(0deg);
  }

  .promo__info-block_title {
    font-size: 53px;
    transform: skewX(349deg);
  }

  .promo__info-block_title p {
    transform: skewX(11deg);
  }

  .promo__info-block_count-num {
    font-size: 92px;
    padding: 17px;
    transform: skewX(0deg);
  }

  .promo__info-block_count-num::before {
    left: -5px;
    width: 29px;
    height: 63px;
    transform: skewX(349deg);
  }

  .promo__info-block_count-num::after {
    transform: skewX(349deg);
  }

  .promo__info-block_count-type {
    transform: skewX(0deg);
    font-size: 48px;
  }

  .promo__info-block_count-type::before {
    height: 32px;
    left: -3px;
    width: 15px;
    transform: skewX(349deg);
  }

  .promo__info-block_count-type::after {
    transform: skewX(349deg);
  }

  .promo__info-block_subtitle {
    font-size: 40px;
  }

  .promo__info-block_subtitle p {
    transform: skewX(0deg);
  }

  .promo__info-block_subtitle p:last-child {
    display: none;
  }

  .promo__info-block_btns {
    margin-left: auto;
    margin-right: auto;
    transform: skewX(349deg);
    margin-bottom: 93px;
  }

  .popup-main {
    width: 345px;
    height: 586px;
  }

  .popup {
    height: 586px;
  }
}